@charset "UTF-8";

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    color: #757575;
}
a:hover{
    text-decoration: underline;
}
img{
    vertical-align: top;
}
input{
    outline: none;
    border: none;
 }
 body{
     color: #606060;
     font-size: 12px;
 }
//网页头部--个人信息
.header{
    width: 1000px;
    height: 27px;
    line-height: 27px;
    margin: 0 auto;
    background: #e6e6e6;
    .my-infor{
        float: right;
        display: flex;
        .name{
            padding: 0 8px 0 6px;
        }
        a{
            padding: 0 8px 0 6px;
        }
        .myacc{
            position: relative;
            .account{
                padding: 0 8px 0 6px;
                width: 80px;
            }
            .part{
                display: none;
                position: absolute;
                left: -158px;
                top:26px;
                padding: 2px 2px;
                width: 221px;
                height: 162px;
                background-color: #fff;
                border: 1px solid #8cb91e;
            }
            .part p{
                margin-bottom: 5px;
                height: 25px;
                background: #f5f5f5; 
                padding-left: 15px;
                padding-right: 15px;   
            }
            .part p span{
                float: left;
            }
            .part>p>a{
                float: right;
                color: #8cb91e;
            }
            .part ul{
                padding-left: 15px;
                padding-right: 15px;
            }
            .part .nodo{
                float: left;
                line-height: 25px;
            }
            .part .nodo>li>a{
                padding-left: 0;
            }
            .part .myin{
                float: right;
                line-height: 25px;
            }
            .part>ul>li>a:hover{
                color: #8cb91e;
            }
        }
        .myacc:hover{
            background: #8cb91e;   
        }
        .myacc:hover .account{
            color: #fff;
        }
        .myacc:hover .part{
            display: block;
        }
        
    }
}
//网页logo--购物进度
.logo{
    width: 1000px;
    height: 95px;
    margin: 0 auto;
    margin-top:10px;
    h3{
       cursor: pointer;
       float: left;
    }
    .schedule{
        float: right;
        margin-top: 26px;
        width: 648px;
        img{
            width: 648px;
        }
    }
}
//网页主页内容，购物车商品
.main{
    width: 1000px;
    margin: 0 auto;
    height: auto;
    .mycart{
        height: 41px;
        margin: 4px 0px 8px;
        padding-bottom: 5px;
        border-bottom: 1px solid #e6e6e6;
        p{
            float: left;
            height: 36px;
            font-size: 20px;
        }
        a{
            margin-top: 8px;
            color: #666;
            float: right;
        }
    
    }
    .list{
        height: 34px;
        line-height: 34px;
        border-bottom: 2px solid #e6e6e6;
        li{
            color: #7abd54;
            border-bottom: 2px solid #7abd54;
            height: 34px;
            padding: 0 25px;
            width: 180px;
            font-size: 16px;
        }
    }
    .titleline{
        margin-top: 15px;
        height: 30px;
        line-height: 19px;
        padding: 5px 0px 6px 0px;
        display: flex;
        text-align: center;
        color: #3c3c3c;
        li:nth-child(1){
            width: 512px;
        }
        li:nth-child(2),li:nth-child(4){
            width: 108px;
        }
        li:nth-child(3){
            width: 148px;
        }
        li:nth-child(5){
            width: 124px;
        }
    }
    .goods-attr{
        margin-top: 20px;
        height: auto;
        .goods{
            border: 1px solid #ccc;
            background: #fcfcfc;
            height: 90px;
            margin-top: 10px;
            #check{
                margin: 35px 15px 0 20px;
                float: left;
            }
            dl{
                padding-top: 15px;
                height: 77px;
                width: 460px;
                float: left;
            }
            dl dt{
                margin-right: 15px;
                width: 60px;
                height: 60px;
                border: 1px solid #DCDDDD;
                float: left;
            }
            dl dt a{
                display: inline-block;
            }
            dl dt a img{
                width: 60px;
                height: 60px;
            }
            dl dd{
                float: left;
            }
            dl dd a{
                display: inline-block;
                width: 320px;
                line-height: 21px;
                color: #595757;
                text-align: left;
            }
            dl dd a:hover{
                color: #8cb91e;
            }
            .price{
                width: 108px;
                padding-top: 30px;
                text-align: center;
                float: left;
            }
            .number{
                width: 148px;
                padding-top: 32px;
                padding-left: 30px;
                float: left;
            }
            .number a{
                display: inline-block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background: rgb(242, 243, 243);
                text-align: center;
                line-height: 20px;
            }
            .number a:hover{
                text-decoration: none;
            }
            .number .content{
                width: 36px;
                height: 23px;
                border: 1px solid #DCDDDD;
                margin: 0px 5px;
                text-align: center;
            }
            .subtotal{
                color: #E9001C;
                font-weight: bold;
                text-align: center;
                padding-top: 32px;
                width: 108px;
                float: left;
            }
            .operate{
                width: 124px;
                text-align: center;
                float: left;
                padding-top: 32px;
            }
            .operate .delete:hover{
                color: #8cb91e;
            }
            
        }

    }
    .all{
        width: 1000px;
        height: 20px;
        margin-top: 10px;
        overflow: hidden;
        .all-left{
            float: left;
            span{
                margin-right: 20px;
            }
            a{
                margin-right: 20px;
            }
        }
        .all-right{
            width: 310px;
            float: right;
            p:nth-child(1){
                float: left;
            }
            .allnums{
                font-size: 18px;
                color: #e9001c;
            }
            p:nth-child(2){
                float: right;
            }
            .totalsum{
                text-align: right;
                color: #e9001c;
                font-size: 18px;
                display: inline-block;
                width: 120px;
            }
        }
        
    }
}

//提示语
.tips{
    width: 1000px;
    height: 108px;
    margin: 0 auto;
    margin-top: 50px;
    border: 1px solid #e1e1e1;
    padding: 10px;
    overflow: hidden;
    p{
        color: rgb(140, 185, 30);
    }
    .last{
        margin-left: 17px;
    }
}
//网页底部
.foot{
    width: 1000px;
    height: auto;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 50px;
    a:hover{
        color: #8cb91e;
    }
    .infor2{
        margin-top: 10px;
        color: #999;
        i{
            display: inline-block;
            width: 20px;
            height: 20px;
            cursor: pointer;
        }
    }
    
}